
import { showModalDialog } from "@/helpers/dialog";
import { computed, defineComponent, onMounted, ref, Ref } from "vue";
import ButtonIcon from "@/components/primitive/ButtonIcon.vue";
import { Icon } from "@/assets/icons";
import { useStore } from "@/store";
import { AppState } from "@/store/state";

export default defineComponent({
  name: "FileMenu",
  components: {
    ButtonIcon,
  },
  emits: ["close"],
  setup(_, context) {
    const store = useStore();
    const dialog: Ref = ref(null);
    const onClose = () => {
      context.emit("close");
    };
    onMounted(() => {
      showModalDialog(dialog.value, onClose);
    });
    const onNewFile = () => {
      store.resetRecord();
      context.emit("close");
    };
    const onOpen = () => {
      store.openRecord();
      context.emit("close");
    };
    const onSave = () => {
      store.saveRecord({ overwrite: true });
      context.emit("close");
    };
    const onSaveAs = () => {
      store.saveRecord();
      context.emit("close");
    };
    const onCopyKIF = () => {
      store.copyRecordKIF();
      context.emit("close");
    };
    const onCopyCSA = () => {
      store.copyRecordCSA();
      context.emit("close");
    };
    const onCopyUSI = () => {
      store.copyRecordUSIAll();
      context.emit("close");
    };
    const onCopySFEN = () => {
      store.copyBoardSFEN();
      context.emit("close");
    };
    const onPaste = () => {
      store.showPasteDialog();
      context.emit("close");
    };
    const states = computed(() => {
      return {
        newFile: store.appState === AppState.NORMAL,
        open: store.appState === AppState.NORMAL,
        save: store.appState === AppState.NORMAL,
        saveAs: store.appState === AppState.NORMAL,
        paste: store.appState === AppState.NORMAL,
      };
    });
    return {
      dialog,
      Icon,
      onClose,
      onNewFile,
      onOpen,
      onSave,
      onSaveAs,
      onCopyKIF,
      onCopyCSA,
      onCopyUSI,
      onCopySFEN,
      onPaste,
      states,
    };
  },
});
